.profile .hyukmin-profile-box {
    width: 900px;
    padding: 45px 20px;
    border-bottom: 1px solid #3d3d3d;
}

.profile .code-wrapper {
    letter-spacing: 0.2em;
    line-height: 1.5em;
}

.profile .set-hyukmin-profile-box {
    width: 900px;
    padding: 0px 20px;
    border-bottom: 1px solid #3d3d3d;
    background-color: #2c2c2c;
    outline: none;
    max-height: 0px;
    opacity: 0;
    overflow: hidden;
    transition-duration: 0.7s;
}

.profile .set-hyukmin-profile-box.visible {
    opacity: 1;
    padding: 30px 20px;
    max-height: 220px;
}