.table-line {
    display: flex;
    border-bottom: 1px solid #3d3d3d;
    font-size: 16px;
}

.table-line-title {
    width: 216px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2c2c2c;
}

.table-line-content {
    padding: 45px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
}